import * as pathUtil from '@antv/path-util';
import { Category, Linear } from '@antv/scale';
import { map, each, isEqual, head } from '@antv/util';
import { isArray } from 'util';

type Point = [number, number];

/**
 *  path
 * @param points
 */
function pointsToPath(points: Point[]): any[][] {
  return map(points, (p: Point, idx: number) => {
    const command = idx === 0 ? 'M' : 'L';
    const [x, y] = p;
    return [command, x, y];
  });
}

/**
 *  path
 * @param points
 */
export function getLinePath(points: Point[]): any[][] {
  return pointsToPath(points);
}

/**
 * 
 * @param points
 */
export function getSmoothLinePath(points: Point[]): any[][] {
  if (points.length <= 2) {
    // 
    return getLinePath(points);
  }

  const data = [];

  each(points, (p) => {
    // 
    if (!isEqual(p, data.slice(data.length - 2))) {
      data.push(p[0], p[1]);
    }
  });

  const path = pathUtil.catmullRom2Bezier(data, false);
  const [x, y] = head(points);
  path.unshift(['M', x, y]);

  return path;
}

/**
 *  path scale 
 * @param data
 * @param width
 * @param height
 * @param smooth
 */
export function dataToPath(data: number[], width: number, height: number, smooth: boolean = true): any[][] {
  //  scale  y 
  const y = new Linear({
    values: data,
  });

  const x = new Category({
    values: map(data, (v, idx) => idx),
  });

  const points = map(data, (v: number, idx: number) => {
    return [x.scale(idx) * width, height - y.scale(v) * height] as [number, number];
  });

  return smooth ? getSmoothLinePath(points) : getLinePath(points);
}

export function dataToRectPath(data: number[], width: number, height: number): any[][] {
  //  scale  y 
  const y = new Linear({
    values: data,
  });

  const x = new Category({
    values: map(data, (v, idx) => idx),
  });

  const points = map(data, (v: number, idx: number) => {
    return [x.scale(idx) * width, height - y.scale(v) * height] as [number, number];
  });

  const rectPoints = []
  for (let i = 0; i < points.length; i++) {
    const point = points[i]
    const param = {
      x: point[0],
      y: point[1],
      y0: height,
      size: 5
    }
    const rectPoint = getRectPoints(param)
    rectPoints.push(...rectPoint)
  }

  return getRectPath(rectPoints)
}

/**
 *  area  px 
 * @param data 
 * @param width 
 * @param height 
 */
export function getAreaLineY(data: number[], height: number): number {
  const y = new Linear({
    values: data,
  });

  const lineY = Math.max(0, y.min);
  return height - y.scale(lineY) * height;
}

/**
 *  path  area path
 * @param path
 * @param width
 * @param height
 */
export function linePathToAreaPath(path: any[][], width: number, height: number, data: number[]): any[][] {
  const areaPath = [...path];

  const lineYPx = getAreaLineY(data, height);

  areaPath.push(['L', width, lineYPx]);
  areaPath.push(['L', 0, lineYPx]);
  areaPath.push(['Z']);

  return areaPath;
}

/**
 * @ignore
 * 
 * @param pointInfo 
 * @returns rect points 
 */
export function getRectPoints(pointInfo): { x: number, y: number }[] {
  const { x, y, y0, size } = pointInfo;
  //  4 
  // 1. x, y 
  // 2. yx
  // 3. xy
  // 4. x, y 
  let yMin;
  let yMax;
  if (isArray(y)) {
    [yMin, yMax] = y;
  } else {
    yMin = y0;
    yMax = y;
  }

  let xMin;
  let xMax;
  if (isArray(x)) {
    [xMin, xMax] = x;
  } else {
    xMin = x - size / 2;
    xMax = x + size / 2;
  }

  const points = [
    { x: xMin, y: yMin },
    { x: xMin, y: yMax },
  ];

  // 
  // 1 ---- 2
  // |      |
  // 0 ---- 3
  points.push({ x: xMax, y: yMax }, { x: xMax, y: yMin });

  return points;
}

/**
 * @ignore
 *  path
 * @param points 
 * @param isClosed path 
 * @returns  path
 */
export function getRectPath(points: { x: number, y: number }[], isClosed: boolean = true) {
  const path = [];
  const firstPoint = points[0];
  path.push(['M', firstPoint.x, firstPoint.y]);
  for (let i = 1, len = points.length; i < len; i++) {
    path.push(['L', points[i].x, points[i].y]);
  }
  //  shape="line" path  lineCap 
  if (isClosed) {
    path.push(['L', firstPoint.x, firstPoint.y]); // 
    path.push(['z']);
  }
  return path;
}
